<template>
  <div class="book">
    <loading v-show="isLoading" />
    <van-nav-bar
      title="书籍详情"
      left-text="返回"
      left-arrow
      fixed
      @click-left="goPage"
      @click-right="goPage('Bookrack')"
    >
      <template #right>
        <svg
          t="1594949373600"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="15150"
          width="24"
          height="24"
        >
          <path
            d="M264.154 952.655H64.31V224.11h199.844v728.545h-0.001z m-34.2-691.413H98.816v67.142h131.14v-67.142z m96.516-196.7h198.267v888.045H326.47V64.542z m168.989 34.214H360.143v69.235h135.315V98.756z m459.399 794.012L764.845 954.68 539.126 261.973l190.01-61.912 225.722 692.707zM708.125 245.964L583.438 286.59l20.798 63.834 124.686-40.627-20.797-63.833z m0 0"
            p-id="15151"
            fill="#B0C4DE"
          />
        </svg>
        <svg
          t="1596588838793"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="5289"
          width="24"
          height="24"
          @click="goPage('Bookstore')"
        >
          <path
            d="M691.2 691.2m-332.8 0a332.8 332.8 0 1 0 665.6 0 332.8 332.8 0 1 0-665.6 0Z"
            fill="#B0C4DE"
            p-id="5290"
          />
          <path
            d="M730.624 947.712H294.912c-96.768 0-123.392-32.256-123.392-133.12V358.4c0-14.336 11.264-25.6 25.6-25.6s25.6 11.264 25.6 25.6v456.192c0 73.216 3.072 81.92 72.192 81.92h435.712c69.12 0 81.92-8.192 81.92-81.92V358.4c0-14.336 11.264-25.6 25.6-25.6s25.6 11.264 25.6 25.6v456.192c0 100.864-36.352 133.12-133.12 133.12z"
            fill="#B0C4DE"
            p-id="5291"
          />
          <path
            d="M620.032 934.912c-7.168 0-12.8-5.632-12.8-12.8v-243.2c0-25.6-5.12-26.112-33.28-26.112h-122.88c-28.16 0-23.552 0.512-23.552 26.112v243.2c0 7.168-5.632 12.8-12.8 12.8s-12.8-5.632-12.8-12.8v-243.2c0-44.032 11.776-51.712 49.152-51.712h122.88c37.376 0 58.88 7.68 58.88 51.712v243.2c0 7.168-5.632 12.8-12.8 12.8zM90.112 434.688c-7.68 0-15.36-3.584-20.48-10.24-8.704-11.264-6.144-27.136 5.12-35.84l422.912-317.952c11.264-8.704 27.136-6.144 35.84 5.12s6.144 27.136-5.12 35.84L105.472 429.568c-4.608 3.584-9.728 5.12-15.36 5.12z"
            fill="#B0C4DE"
            p-id="5292"
          />
          <path
            d="M935.424 434.688c-5.632 0-10.752-2.048-15.36-5.12L497.664 111.616c-11.264-8.704-13.824-24.576-5.12-35.84s24.576-13.312 35.84-5.12l422.912 317.952c11.264 8.704 13.824 24.576 5.12 35.84-5.632 6.656-13.312 10.24-20.992 10.24zM287.232 550.4c-7.168 0-12.8-5.632-12.8-12.8V392.192c0-4.096-0.512-7.68 2.56-10.24l166.912-129.024c5.632-4.096 12.8-3.072 17.408 2.56 4.096 5.632 5.632 13.824 0 17.92L300.032 398.336V537.6c0 7.168-5.632 12.8-12.8 12.8z m0 64c-7.168 0-12.8-5.632-12.8-12.8V588.8c0-7.168 5.632-12.8 12.8-12.8s12.8 5.632 12.8 12.8v12.8c0 7.168-5.632 12.8-12.8 12.8z"
            fill="#B0C4DE"
            p-id="5293"
          />
        </svg>
      </template>
    </van-nav-bar>
    <div class="content">
      <div class="book-content clearfix">
        <div class="img-box fl">
          <img class="auto-img v-img" :src="getImg(bookData.cover)" alt="图片未加载..." />
        </div>
        <div class="content-box fl">
          <div class="text-box clearfix">
            <div class="book-title">{{bookData.title}}</div>
            <div class="book-msg clearfix">
              <span class="book-name fl">{{bookData.author}}</span>
              <span class="book-num fl">{{bookData.wordCount | num}}万字</span>
            </div>
            <div class="moods">
              <span>追人气 {{bookData.latelyFollower | num}}万</span>
              <span>读者留存率 {{bookData.retentionRatio}}%</span>
            </div>
            <div class="text-type clearfix">
              <span class="fl">{{bookData.cat}}</span>
              <span class="fr">{{bookData.majorCate}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="text-content">
        <div class="text-tit">内容简介</div>
        <div class="v-text">{{bookData.longIntro}}</div>
      </div>
      <div class="catalog">
        <van-cell title="目录" is-link :value="firstCatalog" @click="isShowFn" />
      </div>
      <div class="discuss">
        <div class="discuss-box clearfix">
          <div class="fl">热门书评</div>
          <div class="tips-text fr" @click="tipsFn">写评论</div>
        </div>
        <div class="discuss-content" @click="tipsFn">
          <div
            class="discuss-item"
            v-for="(discuss,discussIndex) in discussData"
            :key="discussIndex"
          >
            <div class="nick-name">{{discuss.author.nickname}}</div>
            <div class="text-mini">{{discuss.title}}</div>
            <van-rate class="star" v-model="discuss.rating" readonly color="steelblue" allow-half />
            <div class="discuss-text">{{discuss.content}}</div>
          </div>
        </div>
      </div>
    </div>
    <van-popup class="v-popup" v-model="isShow" round position="bottom">
      <div class="popup-text">目录</div>
      <van-list
        class="popup-list"
        v-model="load.loading"
        :finished="load.finished"
        :finished-text="load.loadingMsg"
        @load="onLoad"
      >
        <div class="popup-item" v-for="(li,j) in catalog" :key="j" @click="goRead(j)">{{li.title}}</div>
      </van-list>
    </van-popup>
    <footer class="foot">
      <span
        class="addBook"
        :class="[isActive ? 'active' : '']"
        ref="addBook"
        @click="saveBook"
      >{{isActive ? '撤出书架':'加入书架' }}</span>
      <span class="look" @click="goPage('Read')">开始阅读</span>
    </footer>
  </div>
</template>

<script>
import loading from "../components/loading";
import "../assets/css/book.less";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("saveDataFn");
export default {
  components: { loading },
  data() {
    return {
      isLoading: false,
      // 书籍详情数据
      bookData: {},
      // 是否显示目录
      isShow: false,
      // 目录列表,
      // 所有书籍列表
      allCatalog: [],
      // 默认加载书籍列表
      catalog: [],
      firstCatalog: "",
      // 默认加载目录数量
      loadCount: 50,
      discussData: [],
      load: {
        // 是否加载 false表示加载
        // true为停止加载
        loading: false,
        finished: false,
        loadingMsg: "已经到底啦"
      },
      // 书籍link,用于加载小说对应章节内容
      msg: {
        id: "",
        link: "",
        book: "",
        name: "",
        linkIndex: 0
      },
      isActive: false,
      startValue: 3.5,
      // 书架数据
      bookRackData: []
    };
  },
  created() {
    this.msg.id = this.$route.query.id;
    if (localStorage.getItem("bookRackData")) {
      let bookRackData = localStorage.getItem("bookRackData").split(",");
      if (bookRackData.length > 1) {
        bookRackData.forEach(v => {
          this.bookRackData.push(v);
        });
      } else {
        this.bookRackData = [bookRackData];
      }
      this.bookRackData.forEach(v => {
        if (v == this.msg.id) {
          this.isActive = true;
          return;
        }
      });
    }
    this.getCatalog();
    this.getDiscuss(this.msg.id);
    this.getBookData();
  },
  methods: {
    ...mapMutations(["saveData", "removeData"]),
    // 返回 | 跳转到书架 | 跳转阅读页面
    goPage(name) {
      if (name == "Bookrack" || name == "Bookstore") {
        this.$router.push({ name: name });
      } else if (name == "Read") {
        this.$router.push({ name: name, query: this.msg });
      } else {
        this.$router.go(-1);
      }
    },
    // 目录跳转到阅读页面
    goRead(index) {
      this.msg.linkIndex = index;
      this.msg.link = this.catalog[this.msg.linkIndex].link;
      this.$router.push({ name: "Read", query: this.msg });
    },
    // 获取书籍详情数据
    getBookData() {
      this.isLoading = true;
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/book-info/" + this.msg.id
      })
        .then(result => {
          if (result.status == 200) {
            this.bookData = result.data;
            this.msg.book = this.bookData.title;
            this.msg.name = this.bookData.author;
          }
        })
        .catch(err => {
          this.$toast("获取数据出错,请重新加载");
          this.isLoading = false;
        });
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    },
    // 获取目录列表,加载目录
    getCatalog() {
      this.isLoading = true;
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/book-sources",
        params: {
          view: "summary",
          book: this.msg.id
        }
      })
        .then(result => {
          this.isLoading = true;
          if (result.status == 200) {
            let bookId = result.data[0]._id;
            this.axios({
              method: "get",
              url: "http://novel.kele8.cn/book-chapters/" + bookId
            })
              .then(result => {
                if (result.status == 200) {
                  this.allCatalog = result.data.chapters;
                  this.catalog = this.allCatalog.splice(0, this.loadCount);
                  this.firstCatalog = this.catalog[0].title;
                  this.msg.link = this.catalog[this.msg.linkIndex].link;
                  if (this.allCatalog.length > 0) {
                    this.load.loading = false;
                  } else {
                    this.load.loading = true;
                    this.load.finished = true;
                  }
                  if (this.catalog.length == 0) {
                    this.load.loadingMsg = "";
                  }
                  this.isLoading = false;
                }
              })
              .catch(err => {
                this.isLoading = false;
                this.$toast("目录数据获取失败");
              });
          }
        })
        .catch(err => {
          this.isLoading = false;
          this.$toast("目录数据获取失败");
        });
    },
    // 显示目录
    isShowFn() {
      this.isShow = true;
    },
    // 获取热评
    getDiscuss(id) {
      this.isLoading = true;
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/book/reviews",
        params: {
          book: id,
          start: 0,
          limit: 3
        }
      })
        .then(res => {
          if (res.status == 200) {
            this.discussData = res.data.reviews;
          }
        })
        .catch(err => {
          this.isLoading = false;
          this.$toast("获取数据出错,请重新加载");
        });
    },
    // 懒加载目录数据
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        // 添加章节
        this.catalog.push(...this.allCatalog.splice(0, this.loadCount));
        // 默认加载数据
        this.load.loading = false;
        // 如果没有数据则停止加载
        if (this.allCatalog.length == 0) {
          this.load.loading = true;
          this.load.finished = true;
        }
      }, 500);
    },
    // 加入书架
    saveBook() {
      let id = this.msg.id;
      this.isActive = !this.isActive;
      if (this.isActive) {
        this.$toast("已加入书架");
        if (this.bookRackData.indexOf(id) != -1) return;
        this.bookRackData.push(id);
      } else {
        this.$toast("已撤出书架");
        let index = this.bookRackData.indexOf(id, 0);
        this.bookRackData.splice(index, 1);
      }
      localStorage.setItem("bookRackData", this.bookRackData);
      if (localStorage.getItem("bookRackData").length == 0) {
        localStorage.removeItem("bookRackData");
      }
    },
    // 提示
    tipsFn() {
      this.$toast("下载最新版追书神器APP体验更多功能");
    }
  }
};
</script>